import React, { useState } from 'react';

import './DivButtonTest.css';

const DivButtonTest = () => {

    const [buttonClicked, setButtonClicked] = useState(0);
    const [divClicked, setDivClicked] = useState(0);

    const handleButtonClick = (event) => {
        console.log(event)
        setButtonClicked(counter => counter + 1);
    };

    const handleDivClick = (event) => {
        console.log(event)
        setDivClicked(counter => counter + 1);
    };

    return(
        <div className="Entry">
            <div className="EntryTitle">
                <h2>Div-Button Test</h2>
            </div>
            <div className="Entry Purpose">
                <b>Purpose: </b>To discover the level of interconnectedness off elements on a div.
                <br />
                If a button and a div both have onClick handlers, how can we specifically manage both?
            </div>
            <div className="ReactionDiv">
                <div className="DivClick">The div was clicked {divClicked} times </div>
                <div className="ButtonClick">The button was clicked {buttonClicked} times </div>
            </div>
            <div className="TestDiv" onClick={handleDivClick}>
                <div>This is some content in here</div>
                <div>There is some more content here</div>
                <div>
                    <button className="Buttz" onClick={handleButtonClick}>
                        CLICK
                    </button>
                </div>
            </div>
        </div>
    )
};

export default DivButtonTest;